.mdl-container{position:fixed;display:table;height:100%;width:100%;top:0;left:0;-ms-transform:scale(0);transform:scale(0);z-index:1}.mdl-container.mdl-sh{z-index:0;-ms-transform:scale(1);transform:scale(1)}.mdl-container.mdl-sh .mdl-overlay{background:#373a47}.mdl-container.mdl-sh .mdl{animation:c .5s cubic-bezier(.165,.84,.44,1) forwards}.mdl-container.mdl-sh+.content{z-index:1;animation:a .5s cubic-bezier(.165,.84,.44,1) forwards}.mdl-container.mdl-sh.out .mdl{animation:d .5s cubic-bezier(.165,.84,.44,1) forwards}.mdl-container.mdl-sh.out+.content{animation:b .5s cubic-bezier(.165,.84,.44,1) forwards}.mdl-container .mdl-overlay{display:table-cell;background:#373a47;text-align:center;vertical-align:middle;z-index:2}.mdl-container .mdl{width:45%;background:#fff;padding:.5rem;z-index:3;display:inline-block;border-radius:3px;font-weight:300;position:relative;overflow:auto}@media only screen and (max-width:1366px){.mdl{width:80%}}@media only screen and (max-height:1036px){.mdl{height:45%}}@keyframes a{0%{transform:translateY(0)}to{transform:translateY(-100%)}}@keyframes b{0%{transform:translateY(-100%)}to{transform:translateY(0)}}@keyframes c{0%{transform:translateY(200%)}to{transform:translateY(0)}}@keyframes d{0%{transform:translateY(0)}to{transform:translateY(200%)}}